<template>
  <div>
    <div class="nv-row">
      <div class="nv-col w-full lg mb-base">
        <nv-card id="position-loading-container" class="vs-con-loading__container">
          <div class="flex justify-between mb-base">
            <div class="flex justify-left items-center">
              <span class="item-circle mr-4 w-12 h-12" :style="{background: `rgba(var(--vs-primary),.15)`}">
                <i class="far fa-coins fa-lg text-primary"></i>
              </span>
              <div>
                <h4 class="mb-1">{{ $t('NET_OUTSTANDING_POSITION') }}</h4>
              </div>
            </div>
            <div>
              <vs-dropdown vs-trigger-click class="cursor-pointer">
                <small class="flex cursor-pointer">{{ $t('CHOOSE_CURRENCY') }} <i class="far fa-chevron-down ml-2"></i></small>
                <vs-dropdown-menu class="w-32">
                  <vs-dropdown-item v-for="currency in currencyList" :key="currency.uomId" @click="pickCurrency(currency.uomId)">{{currency.abbreviation}}</vs-dropdown-item>
                </vs-dropdown-menu>
              </vs-dropdown>
            </div>
          </div>
          <div class="flex flex-row justify-between px-8 pb-4">
            <p class="text-center">
              <span class="block">{{ $t('TOTAL_AVAILABLE') }}</span>
              <span class="text-2xl" :class="negativeNumberStyle(totalAccounts)">{{ totalAccounts | currency('') | placeholder('-') }} {{ currencyAbbreviation }}</span>
            </p>
            <p class="text-center">
              <span class="block">{{ $t('TOTAL_OUTSTANDING') }}</span>
              <span class="text-2xl" :class="positiveNumberStyle(totalLoans)">{{ totalLoans | currency('') | placeholder('-') }} {{ currencyAbbreviation }}</span>
            </p>
            <p class="text-center">
              <span class="block">{{ $t('FIXED_DEPOSITS') }}</span>
              <span class="text-2xl" :class="negativeNumberStyle(totalDeposits)">{{ totalDeposits | currency('') | placeholder('-') }} {{ currencyAbbreviation }}</span>
            </p>
            <p class="text-center">
              <span class="block">{{ $t('OVERALL_NET_CASH') }}</span>
              <span class="text-2xl" :class="negativeNumberStyle(netPosition)">{{ netPosition | currency('') | placeholder('-') }} {{ currencyAbbreviation }}</span>
            </p>
          </div>
        </nv-card>
      </div>
    </div>

    <div class="nv-row">
      <div class="nv-col w-full mb-base">
        <nv-card id="workflow-loading-container" class="vs-con-loading__container">
          <div class="flex justify-left items-center mb-base">
            <span class="item-circle mr-4 w-12 h-12" :style="{background: `rgba(var(--vs-warning),.15)`}">
              <i class="far fa-shapes fa-lg text-warning"></i>
            </span>
            <div>
              <h4 class="mb-1">{{ $t('PENDING_WORKFLOWS') }}</h4>
            </div>
          </div>
          <vs-table :data="workflowTaskTableData" class="dashboard-table">
            <template slot="thead">
              <vs-th>
                {{ $t('WORKFLOW_NAME') }}
              </vs-th>
              <vs-th>
                {{ $t('WORKFLOW_TYPE') }}
              </vs-th>
              <vs-th>
                {{ $t('ACTION') }}
              </vs-th>
              <vs-th>
                {{ $t('COUNT') }}
              </vs-th>
            </template>
            <template slot-scope="{data}">
              <vs-tr :key="index" v-for="(tr, index) in data">
                <vs-td>
                  <span>{{ data[index].workflowName }}</span>
                </vs-td>
                <vs-td>
                  <span>{{ data[index].typeName }}</span>
                </vs-td>
                <vs-td>
                  <span>{{ data[index].actionTypeDescription }}</span>
                </vs-td>
                <vs-td>
                  <span>{{ data[index].taskCount }}</span>
                </vs-td>
              </vs-tr>
            </template>
          </vs-table>
        </nv-card>
      </div>
    </div>

    <div class="nv-row">
      <div class="nv-col w-full mb-base">
        <nv-card id="trade-loading-container" class="vs-con-loading__container">
          <div class="flex justify-left items-center mb-base">
            <span class="item-circle mr-4 w-12 h-12" :style="{background: `rgba(var(--vs-danger),.15)`}">
              <i class="far fa-hand-holding-usd fa-lg text-danger"></i>
            </span>
            <div>
              <h4 class="mb-1">{{ $t('OVERDUE_TRADE_FINANCE_PRODUCTS') }}</h4>
            </div>
          </div>
          <vs-table :data="loanTableData" class="dashboard-table">
            <template slot="thead">
              <vs-th sort-key="loanNumber">
                {{ this.$t('LOAN_NUMBER') }}
              </vs-th>
              <vs-th sort-key="typeName">
                {{ this.$t('LOAN_TYPE') }}
              </vs-th>
              <vs-th sort-key="startDate">
                {{ this.$t('START_DATE') }}
              </vs-th>
              <vs-th sort-key="currencyAbbreviation">
                {{ this.$t('CURRENCY') }}
              </vs-th>
              <vs-th sort-key="loanAmount">
                {{ this.$t('LOAN_AMOUNT') }}
              </vs-th>
              <vs-th sort-key="outstandingAmount">
                {{ this.$t('OUTSTANDING_AMOUNT') }}
              </vs-th>
            </template>

            <template slot-scope="{data}">
              <vs-tr :key="index" v-for="(tr, index) in data" :data="tr">
                <vs-td :data="data[index].loanNumber">
                  <router-link :to="{ name: 'ViewOverdueTrustReceiptLoan', params: { id: data[index].loanNumber } }">{{ data[index].loanNumber }}</router-link>
                </vs-td>
                <vs-td :data="data[index].typeName">
                  {{ data[index].typeName | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].startDate">
                  {{ data[index].startDate | formatUnixDate | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].currencyAbbreviation">
                  {{ data[index].currencyAbbreviation | placeholder('-') }}
                </vs-td>
                <vs-td :data="data[index].loanAmount">
                  <span :class="negativeNumberStyle(data[index].loanAmount)">
                    {{ data[index].loanAmount | currency('') | placeholder('-') }}
                  </span>
                </vs-td>
                <vs-td :data="data[index].outstandingAmount">
                  <span :class="negativeNumberStyle(data[index].outstandingAmount)">
                    {{ data[index].outstandingAmount | currency('') | placeholder('-') }}
                  </span>
                </vs-td>
              </vs-tr>
            </template>
          </vs-table>
        </nv-card>
      </div>
    </div>
  </div>
</template>

<script>
import LoadingMixin from '@/mixins/commons/LoadingMixin'
import NumberStyleMixin from '@/mixins/commons/NumberStyleMixin'
import DashboardService from '@/services/nucleus/DashboardService'
import CurrencyService from '@/services/nucleus/CurrencyService'
import TradeFinanceService from '@/services/trade/TradeFinanceService'
import WorkflowService from '@/services/commons/WorkflowService'

export default {
  name: 'dashboard',
  mixins: [LoadingMixin, NumberStyleMixin],
  data: function () {
    return {
      totalAccounts: 0,
      totalLoans: 0,
      totalDeposits: 0,
      netPosition: 0,
      currencyId: '',
      currencyDescription: '',
      currencyAbbreviation: '',
      loanTableData: [],
      workflowTaskTableData: [],
      currencyList: []
    }
  },
  methods: {
    async loadNetPosition () {
      let self = this
      this.startLoading('position-loading-container')
      DashboardService.netPosition({
        currencyId: this.currencyId
      }).then(function (response) {
        self.stopLoading('position-loading-container')
        self.totalAccounts = response.data.totalAccounts || 0
        self.totalLoans = response.data.totalLoans || 0
        self.totalDeposits = response.data.totalDeposits || 0
        self.netPosition = response.data.netPosition || 0
        self.currencyId = response.data.currencyId || ''
        self.currencyDescription = response.data.currencyDescription || ''
        self.currencyAbbreviation = response.data.currencyAbbreviation || ''
      }).catch(function () {
        self.stopLoading('position-loading-container')
      })
    },
    async loadWorkflowInstanceTaskStats () {
      let self = this
      this.startLoading('workflow-loading-container')
      WorkflowService.workflowTaskStats({
      }).then(function (response) {
        self.workflowTaskTableData = response.data.workflowInstanceTaskStatuses || []
        self.stopLoading('workflow-loading-container')
      }).catch(function () {
        self.stopLoading('workflow-loading-container')
      })
    },
    async loadOverdueTradeFinanceLoans () {
      let self = this
      this.startLoading('trade-loading-container')
      TradeFinanceService.overdueLoans({
      }).then(function (response) {
        self.loanTableData = response.data.loanList || []
        self.stopLoading('trade-loading-container')
      }).catch(function () {
        self.stopLoading('trade-loading-container')
      })
    },
    async loadCurrencies () {
      let self = this
      CurrencyService.currencyApps({
      }).then(function (response) {
        self.currencyList = response.data.currencyAppList || []
      })
    },
    pickCurrency (currencyId) {
      this.currencyId = currencyId
      this.loadNetPosition()
    }
  },
  mounted () {
    this.loadNetPosition()
    this.loadWorkflowInstanceTaskStats()
    this.loadOverdueTradeFinanceLoans()
    this.loadCurrencies()
  }
}
</script>
