import Endpoint from '@/services/Endpoint'

export default {
  workflowTaskStats (payload) {
    return Endpoint().get('/s1/connect/workflow/task/stats', {
      params: payload
    })
  },
  workflowTasks (payload) {
    return Endpoint().get('/s1/connect/workflow/task', {
      params: payload
    })
  },
  modifyWorkflowTask (payload) {
    return Endpoint().patch('/s1/connect/workflow/task', payload)
  },
  workflowInstances (payload) {
    return Endpoint().get('/s1/connect/workflow/instance', {
      params: payload
    })
  }
}
